:root {
	--selected-background: #34495e;
	--selected-color: #fff;
	--disabled-color: #bbb;
	--component-background: #fff;
	--component-color: #424242;
	--underlay-background: rgba(66, 66, 66, 0.3);
	--underlay-background: rgba(66, 66, 66, 0.3);

	--error-color: red;
	--success-color: green;

	--short-animation-duration: .2s;
	--short-animation-easing: ease;

	/* Z-index levels */
	--zindex-base: 0;
	--zindex-tooltip: 100;
	--zindex-dropdown: 200;
	--zindex-fixed: 300;
	--zindex-dialog: 400;
	--zindex-alert: 500;
}

.root { }

.outputTooltip {
	left: 2.5em;
	position: absolute;
	top: 2.5em;
}

/* vertical slider */

.vertical .input,
.vertical .track {
	-webkit-transform-origin: 0 0;
	        transform-origin: 0 0;
	-webkit-transform: rotate(-90deg) translate(-100%, 0);
	        transform: rotate(-90deg) translate(-100%, 0);
}

/* disabled and readonly styles */

.disabled .track,
.readonly .track {
	background-color: #bbb;
	background-color: var(--disabled-color);
}

/* required style, only applies to inputs with a label */

.required span::after {
	content: '*';
	color: red;
	color: var(--error-color);
}

/* invalid style */

.invalid .track {
	border-color: red;
	border-color: var(--error-color);
}

.invalid .thumb::before {
	background-color: red;
	background-color: var(--error-color);
}

/* valid style */

.valid .inputWrapper::before {
	border-color: green;
	border-color: var(--success-color);
}

.valid .inputWrapper::after {
	background-color: green;
	background-color: var(--success-color);
}

.focused { }

.inputWrapper {

}

.focused input {
	outline: none;
}

.focused .thumb {
	-webkit-box-shadow: 0 0 7px 3px #34495e;
	        box-shadow: 0 0 7px 3px #34495e;
	-webkit-box-shadow: 0 0 7px 3px var(--selected-background);
	        box-shadow: 0 0 7px 3px var(--selected-background);
	border-color: white;
}

.track {
	background-color: #fff;
	background-color: var(--component-background);
	border: 1px solid #424242;
	border: 1px solid var(--component-color);
	height: 0.75em;
}

.fill {
	background-color: #34495e;
	background-color: var(--selected-background);
}

.input {
	height: 0.75em;
}

.input::-ms-thumb {
	height: 2em;
	width: 2em;
}

.input::-moz-range-thumb {
	height: 2em;
	width: 2em;
}

.input::-webkit-slider-thumb {
	height: 2em;
	width: 2em;
}

.thumb {
	background-color: #fff;
	background-color: var(--component-background);
	border: 1px solid #424242;
	border: 1px solid var(--component-color);
	height: 2em;
	left: 50%;
	margin-left: -1em;
	top: -0.625em;
	width: 2em;
}

.output { }



